<template>
	<div>
		<table>
			<thead>
				<tr>
					<th>标题</th>
					<th>时间</th>
					<th>操作</th>
				</tr>
			</thead>
			<tbody id="table-body">
			<tr v-for="(i,k) in userWz" :key="k">
				<td>{{i.title}}</td>
				<td>{{times(i.time)}}</td>
				<td>
					<button class="btn del" @click="del(i.id)" >删除</button>
				</td>
			</tr>
			</tbody>
		</table>
	</div>
	
</template>

<script>
	export default{
		data() {
			return {
				userWz: []
			}
		},
		methods: {
			init() {
				this.$A.get("/api/querytArticle").then(res =>{
					this.userWz = res.data.data
				})
			},
			times(timestamp){
			   var date = null
			   if(timestamp.length==10){
			   	date = new Date(+timestamp * 1000);
			   }else{
			   	date = new Date(+timestamp);
			   }
			   //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			   var Y = date.getFullYear() + '-';
			   var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
			   var D = (date.getDate() < 10 ? '0'+date.getDate() : date.getDate()) + ' ';
			   var h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
			   var m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
			   var s = (date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds());
			   return Y+M+D+h+m+s;
			},
			del(id){
				let that = this
				this.$A.get("/api/deltWz/"+id).then(res =>{
					that.init()
					alert("删除成功")
				})
			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style>
	
	table {
	    width: 100%;
	}
	td{
		border: 1px double #CBD3DA;
	}
	table thead {
	    background-color: #e3e8ee;
	    color: #646987;
	    font-size: 14px;
	}
	table tbody {
	    background-color: #fff;
	    color: #000;
	    text-align: center;
	}
	table tr {
	    height: 30px;
	    line-height: 30px;
	}
	
	table .btn {
	    padding: 3px 10px;
	    color: #fff;
	    font-size: 12px;
	    border: none;
	    cursor: pointer;
	    outline: none;
	}
	
	table .btn.del {
	    background-color: #d9534f;
	}
	
	table .btn.del:hover {
	    background-color: #c9302c;
	} 
</style>
